<template>
    <div class='change_pwd'>
        <div class="change_pwd_header">
            <div class="back"  @click.stop="back()">
                <img src="../../assets/image/联合42.png" alt="">
            </div>
            <div class="change_pwd_personPwd">修改密码</div>
            <div v-if=" !isshowOne || !isshowTWO || !isshowThree" class='save' @click='pwdSave()'>保存</div>
            <div v-if="isshowOne && isshowTWO && isshowThree"  class='saveTwo' @click='pwdSave()'>保存</div>
        </div>
        <div class='changePwdContainer'>
            <div class='mimachange'>
                <div style='width:5.5rem'>
                    <span class='changePwsLeft'>原密码</span>
                    <input  @blur="delet('delete-oldpwd')" @focus="add('delete-oldpwd')"
                    type="password" v-model="oldpwd"   maxlength="16" class="changePwsLeft_input oldpassword"
                    style="width: calc(100% - 1.35rem - 0.8rem);" placeholder="请输入">
                </div>
                <span class='showoldpwd showpwd' @click.stop='showoldpwd()'><img :src="oldpwdimg" alt=""></span>
                <div class="delete-oldpwd"><img src="../../assets/image/X.png" alt="" @click="deleteMes(0)"></div>
            </div>
            <div class='mimachange'>
                <div style='width:5.5rem'>
                    <span class='changePwsLeft'>新密码</span>
                    <input  @blur="delet('delete-newpwd')" @focus="add('delete-newpwd')"
                    type="password" v-model="newpwd"  maxlength="16" class="changePwsLeft_input newpassword"
                    style="width: calc(100% - 1.35rem - 0.8rem);" placeholder="请输入">
                </div>
                <span class='shownewpwd showpwd' @click.stop='shownewpwd()'><img :src="newpwdimg" alt=""></span>
                <div class="delete-newpwd"><img src="../../assets/image/X.png" alt="" @click="deleteMes(1)"></div>
            </div>
            <div class='mimachange'>
                <div style='width:5.5rem'>
                    <span class='changePwsLeftTwo'>确认新密码</span>
                    <input  @blur="delet('delete-newpwdtwo')"  @focus="add('delete-newpwdtwo')"
                    type="password" v-model="newpwdtwo"   maxlength="16" class="changePwsLeft_input newpasswordtwo"
                    style="margin-left:0.3rem;width: calc(100% - 1.35rem - 0.8rem);" placeholder="请确认">
                </div>
                <span class='shownewpwdtwo showpwd' @click.stop='shownewpwdtwo()'><img :src="newpwdtwoimg" alt=""></span>
                <div class="delete-newpwdtwo"><img src="../../assets/image/X.png" alt="" @click="deleteMes(2)"></div>
            </div>
            <div>
                <span class='changePwsLeftLast' >密码由8-16个字符组成，区分大小写，包含数字、字母或特殊字符（不能包含空格）</span>
            </div>
        </div>
        <div class="pwdError" v-show="isshowerror">
          <div style="position: relative;width: 4.3rem;height: 0.76rem">
            <!-- <img src="../../assets/image/密码错误.png" style="position: relative;width: 4.3rem;height: 0.76rem;"> -->
            <div class="pwdError-mes">{{mess}}</div>
          </div>
        </div>
    </div>
</template>
<script>
import API from '@/api/api'
import $ from 'jquery'
import close from '../../assets/image/眼睛_闭.png'
import open from '../../assets/image/眼睛_开.png'
// 引入图片
export default {
  data () {
    return {
      oldpwd: '',
      newpwd: '',
      newpwdtwo: '',
      mess: '',
      isshowerror: false,
      isShowSave: false,
      isshowOne: false,
      isshowTWO: false,
      isshowThree: false,
      oldpwdimg: close, // 原密码图片
      newpwdimg: close, // 新密码图片
      newpwdtwoimg: close, // 确认新密码图片
      isshowoldpwd: 0, // 判断是否点击显示原密码
      isshownewpwd: 0, // 判断是否点击显示原密码
      isshownewpwdtwo: 0 // 判断是否点击显示原密码
    }
  },
  created () {

  },
  mounted () {
    $('.delete-oldpwd').hide()
    $('.delete-newpwd').hide()
    $('.delete-newpwdtwo').hide()
  },
  methods: {
    // 点击显示密码
    showoldpwd () {
      if (this.isshowoldpwd === 0) {
        this.oldpwdimg = open
        $('.oldpassword').prop('type', 'text')
        this.isshowoldpwd = 1
      } else {
        this.oldpwdimg = close
        $('.oldpassword').prop('type', 'password')
        this.isshowoldpwd = 0
      }
    },
    // 点击显示密码
    shownewpwd () {
      if (this.isshownewpwd === 0) {
        this.newpwdimg = open
        $('.newpassword').prop('type', 'text')
        this.isshownewpwd = 1
      } else {
        this.newpwdimg = close
        $('.newpassword').prop('type', 'password')
        this.isshownewpwd = 0
      }
    },
    // 点击显示密码
    shownewpwdtwo () {
      if (this.isshownewpwdtwo === 0) {
        this.newpwdtwoimg = open
        $('.newpasswordtwo').prop('type', 'text')
        this.isshownewpwdtwo = 1
      } else {
        this.newpwdtwoimg = close
        $('.newpasswordtwo').prop('type', 'password')
        this.isshownewpwdtwo = 0
      }
    },
    // 返回
    back () {
      this.$router.back()
    },
    delet (item) {
      setTimeout(() => {
        $(`.${item}`).hide()
      }, 1)
    },
    add (item) {
      if (item === 'delete-oldpwd') {
        $('.delete-oldpwd').show()
        $('.delete-newpwd').hide()
        $('.delete-newpwdtwo').hide()
      } else if (item === 'delete-newpwd') {
        $('.delete-newpwd').show()
        $('.delete-oldpwd').hide()
        $('.delete-newpwdtwo').hide()
      } else if (item === 'delete-newpwdtwo') {
        $('.delete-newpwdtwo').show()
        $('.delete-oldpwd').hide()
        $('.delete-newpwd').hide()
      }
    },

    // 保存
    pwdSave () {
      if (!this.isshowOne || !this.isshowTWO || !this.isshowThree) {
        return
      }
      if (this.newpwd !== this.newpwdtwo) {
        this.mess = '确认密码与新密码不一致'
        // this.isshowerror = true
        this.$toast(this.mess)
        const that = this
        // setTimeout(() => { that.isshowerror = false }, 5000)
        return
      }
      // var sz =  /[0-9]/;
      // var zm =  /[a-z]/i;
      // var zifu =  /[!@#$%^&*()_+?-！@#￥%……&*（）——+？-]/;
      const reg = /^[0-9a-zA-Z!@#$%^&*()_+?-！@#￥%……&*（）——+？-]+$/
      // if(this.oldpwd.length < 6 || this.newpwd.length < 8 || this.newpwdtwo <
      // 8 || !(sz.test(this.newpwd) && zm.test(this.newpwd) && zifu.test(this.newpwd)) )
      if (this.oldpwd.length < 6 || this.newpwd.length < 8 || this.newpwdtwo < 8 || !reg.test(this.newpwd)) {
        this.mess = '密码格式不正确，请重新输入'
        // this.isshowerror = true
        this.$toast(this.mess)
        const that = this
        setTimeout(() => { that.isshowerror = false }, 5000)
        return
      }
      if (this.newpwd === this.oldpwd) {
        this.mess = '新密码与原密码填写一致，请重新输入'
        // this.isshowerror = true
        this.$toast(this.mess)
        const that = this
        setTimeout(() => { that.isshowerror = false }, 5000)
        return
      }
      API.post('modifyPasswd', {
        oldPwd: this.ENCRYPT.RSAencrypt(this.oldpwd),
        newPwd: this.ENCRYPT.RSAencrypt(this.newpwd)
      }, 'json', (data) => {
        if (data.success) {
          localStorage.setItem('isLogin', false)
          localStorage.removeItem('password')
          localStorage.removeItem('activeItem')
          localStorage.removeItem('classifyList')
          localStorage.removeItem('personId')
          localStorage.removeItem('personName')
          this.$router.replace({
            name: 'login'
          })

          return
        }
        if (data.code === '10052') {
          this.mess = '原密码不正确，请重新输入'
          // this.isshowerror = true
          this.$toast(this.mess)
          const that = this
          setTimeout(() => { that.isshowerror = false }, 5000)
        }
      })
    },
    // 清空
    deleteMes (item) {
      if (item === 0) {
        this.oldpwd = ''
      } else if (item === 1) {
        this.newpwd = ''
      } else if (item === 2) {
        this.newpwdtwo = ''
      }
    }
  },
  watch: {
    oldpwd (val) {
      // eslint-disable-next-line no-param-reassign
      val = val.replace(/[^\u0020-\u007E\u00A0-\u00BE\u2E80-\uA4CF\uF900-\uFAFF\uFE30-\uFE4F\uFF00-\uFFEF\u0080-\u009F\u2000-\u201f\u2026\u2022\u20ac\r\n]/g, '')
      // eslint-disable-next-line no-param-reassign
      val = val.replace(/\s+/g, '')
      // eslint-disable-next-line no-param-reassign
      val = val.replace(/[\u4E00-\u9FA5]/g, '')
      if (this.oldpwd) {
        $('.showoldpwd').css('display', 'flex')
      } else {
        $('.showoldpwd').css('display', 'none')
        $('.oldpassword').prop('type', 'password')
        this.oldpwdimg = close
        this.isshowoldpwd = 0
      }

      if (val.trim() !== '' && val != null) {
        // $('.delete-oldpwd').show();
        this.isshowOne = true
        this.oldpwd = val.trim()
      } else {
        // $('.delete-oldpwd').hide();
        this.isshowOne = false
        this.oldpwd = ''
      }
      this.isshowerror = false
    },
    newpwd (val) {
      // eslint-disable-next-line no-param-reassign
      val = val.replace(/[^\u0020-\u007E\u00A0-\u00BE\u2E80-\uA4CF\uF900-\uFAFF\uFE30-\uFE4F\uFF00-\uFFEF\u0080-\u009F\u2000-\u201f\u2026\u2022\u20ac\r\n]/g, '')
      // eslint-disable-next-line no-param-reassign
      val = val.replace(/\s+/g, '')
      // eslint-disable-next-line no-param-reassign
      val = val.replace(/[\u4E00-\u9FA5]/g, '')
      if (this.newpwd) {
        $('.shownewpwd').css('display', 'flex')
      } else {
        $('.shownewpwd').css('display', 'none')
        $('.newpassword').prop('type', 'password')
        this.newpwdimg = close
        this.isshownewpwd = 0
      }
      if (val.trim() !== '' && val != null) {
        // $('.delete-newpwd').show();
        this.isshowTWO = true
        this.newpwd = val.trim()
      } else {
        // $('.delete-newpwd').hide();
        this.isshowTWO = false
        this.newpwd = ''
      }
      this.isshowerror = false
    },
    newpwdtwo (val) {
      // eslint-disable-next-line no-param-reassign
      val = val.replace(/[^\u0020-\u007E\u00A0-\u00BE\u2E80-\uA4CF\uF900-\uFAFF\uFE30-\uFE4F\uFF00-\uFFEF\u0080-\u009F\u2000-\u201f\u2026\u2022\u20ac\r\n]/g, '')
      // eslint-disable-next-line no-param-reassign
      val = val.replace(/\s+/g, '')
      // eslint-disable-next-line no-param-reassign
      val = val.replace(/[\u4E00-\u9FA5]/g, '')
      if (this.newpwdtwo) {
        $('.shownewpwdtwo').css('display', 'flex')
      } else {
        $('.shownewpwdtwo').css('display', 'none')
        $('.newpasswordtwo').prop('type', 'password')
        this.newpwdtwoimg = close
        this.isshownewpwdtwo = 0
      }
      if (val.trim() !== '' && val != null) {
        // $('.delete-newpwdtwo').show();
        this.isshowThree = true
        this.newpwdtwo = val.trim()
      } else {
        // $('.delete-newpwdtwo').hide();
        this.isshowThree = false
        this.newpwdtwo = ''
      }
      this.isshowerror = false
    }
  }
}
</script>
<style scoped>
    .change_pwd{
        width:100%;
        height:100vh;
        /* overflow: scroll; */
        background:rgba(245,246,248,1);
    }
    .change_pwd_header{
        width:100%;
        background:#fff;
        font-size:0.38rem;
        font-family: PingFang-SC-Medium;
        position: fixed;
        top:0;
        height: 0.94rem;
        z-index: 99;
        text-align: center;
        border-bottom: 0.012rem solid  rgba(222,230,242,1);
    }
    .back{
        width:0.89rem;
        height:0.94rem;
        position: fixed;
        top:0;
        left:0;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 100;
    }
    .back img{
        width:0.19rem;
        height:0.34rem;
    }
    .change_pwd .save{
        position: absolute;
        display: flex;
        width:1.01rem;
        height: 0.94rem;
        right:0;
        top:0;
        justify-content: center;
        align-items: center;
        font-size:0.32rem;
        color:#C2CBD6;
        opacity : 0.7;
    }
    .change_pwd .saveTwo{
        position: absolute;
        display: flex;
        width:1.01rem;
        height: 0.94rem;
        right:0;
        top:0;
        justify-content: center;
        align-items: center;
        font-size:0.32rem;
        color:#6E747E;
        opacity : 1;
    }
    .change_pwd_personPwd{
        line-height: 0.94rem;
        font-size:0.38rem;
        color:#4E4E4E;
    }
    .changePwdContainer{
        margin-top:0.94rem;
        background: rgba(255,255,255,1);
        width:100%;
        /* padding:0 0.34rem; */
    }
    .changePwdContainer>div{
        padding: 0 0.35rem;
        height:1.06rem;
        border-bottom:0.012rem solid rgba(238,238,238,1);
        display: flex;
        align-items: center;
        font-size:0.34rem;
        position: relative;
        justify-content: space-between;
    }
    .changePwdContainer>div input{
        color:#6E747E;
        font-size:0.34rem;
    }
    .changePwdContainer>div:last-child{
        height:1.34rem;
        border:0px
    }
    .changePwdContainer>div .changePwsLeft{
        color:#6E747E;
        display:inline-block;
        width: 1.7rem;
    }
    .changePwdContainer>div .changePwsLeftTwo{
        color:#6E747E;
        display:inline-block;
        width: 1.7rem;
    }
    .changePwdContainer>div .changePwsLeft_input{
        margin-left:0.3rem;
        line-height:0.58rem;
        outline: none;
    }
    .changePwdContainer>div  .changePwsLeftLast{
        color:#AFB8C1;
        display:inline-block;
        /* margin-left: 0.48rem;
        margin-right: 0.43rem; */
        font-size: 0.24rem;
        line-height: 0.4rem;
    }
    .pwdError{
      
        height: 0.76rem;
        width: 100%;
        margin-top: 1.2rem;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
    }
    .pwdError-mes{
      width: 4.3rem;height: 0.76rem;
      background: url('../../assets/image/密码错误.png') 4.3rem 0.76rem;
        color: #fff;
        font-size: 0.24rem;
        display: flex;
        align-items: center;
        justify-content: center;
        /* position: absolute;
        z-index: 2; */

    }
    .delete-oldpwd img{
        width:0.42rem;
        height:0.42rem;
        display: block;
    }
    .delete-newpwd img{
        width:0.42rem;
        height:0.42rem;
        display: block;
    }
    .delete-newpwdtwo img{
        width:0.42rem;
        height:0.42rem;
        display: block;
    }
    .showpwd{
        display: none;
        align-items: center;
        position:absolute;
        bottom:0.35rem;
        right:1rem;
        height:0.36rem;
    }
    .showpwd img{
        display: flex;
        width:0.36rem;
    }
</style>
<style>
    .change_pwd ::-webkit-input-placeholder { /* WebKit, Blink, Edge */
        color:#C2CBD6;
        line-height: normal;
        font-size : 0.34rem;
    }
</style>



